<template>
    <div class="container" @click="handleGallaryClick">
        <div class="wrapper">            
            <mt-swipe>
                <mt-swipe-item class="" v-for="(item,index) of list" :key="index">
                    <a href="">
                        <img class="gallary-img" :src="item" />
                    </a>
                </mt-swipe-item>            
            </mt-swipe>
            <div class="swiper-pagination">1/30</div>
        </div>
    </div>
</template>

<script>
export default {
    name:"CommonGallary",
    data(){
        return {
            list: ["http://img1.qunarzz.com/sight/p0/201404/23/04b92c99462687fa1ba45c1b5ba4ad77.jpg_800x800_70debc93.jpg",
             "http://img1.qunarzz.com/sight/p0/1709/76/7691528bc7d7ad3ca3.img.png_800x800_9ef05ee7.png"]
        }
    },
    methods: {
        handleGallaryClick () {
            this.$emit('close')
        }
    }
}
</script>

<style lang="stylus" scoped>
    
    .container
        display: flex
        flex-direction: column
        justify-content: center
        align-items center
        z-index: 999
        position: fixed
        left: 0
        right: 0
        top: 0
        bottom: 0
        background #000
        .wrapper            
            width: 100%
            height 5.52rem
            .gallary-img
                width: 100%
                height 100%
            .swiper-pagination
                color: #fff                
                text-align center
                padding .213333rem 0



            
</style>



