<template>
    <div class="swiper">
        <mt-swipe>
            <mt-swipe-item class="" v-for="item of list" :key="item.id">
                <a href="">
                    <img class="swiper-img" :src="item.imgUrl" />
                </a>
            </mt-swipe-item>            
        </mt-swipe>
    </div>
</template>

<script>
import Vue from 'vue'
import { Swipe, SwipeItem } from 'mint-ui';

import 'mint-ui/lib/style.css'

Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);

export default {
    name: 'HomeSwiper',
    props: {
        list: Array
    },
}
</script>

<style lang="stylus" scoped>

  .swiper    
    height: 200px   

  .swiper-img
    width: 100%
    height:100%
</style>


