<template>
    <div class="list" ref="wrapper">
        <div class="content">
            <div class="area">
                <div class="title border-topbottom">当前城市</div>
                <div class="button-list">
                    <div class="button-wrapper">
                        <div class="button">
                            {{this.currentCity}}
                        </div>
                    </div>
                   
                </div>
            </div>
            <div class="area">
                <div class="title">热门城市</div>
                <div class="button-list">
                    <div class="button-wrapper" 
                    v-for="item of hot" :key="item.id"
                    @click="handleCityClick(item.name)"
                    >
                        <div class="button">{{item.name}}</div>
                    </div>
                    
                </div>
            </div>
            <div class="area" v-for="(item,key) of cities" :key="key" :ref="key">
                <div class="title border-topbottom">{{key}}</div>
                <div class="item-list">
                    <div class="item border-bottom" 
                    v-for="innerItem of item" 
                    :key="innerItem.id"
                    @click="handleCityClick(innerItem.name)"              
                    >
                        {{innerItem.name}}
                    </div>
                   
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
import {mapState,mapMutations} from 'vuex'

export default {
    name:'CityList',
    computed:{
        //展开运算符
        ...mapState({
            currentCity:'city'  //将vuex中的city映射到currentCity计算属性中
        })
    },
    methods:{
        handleCityClick(city){
            //派发一个名称为changeCity的action
            //this.$store.dispatch('changeCity',city)
            //也可以直接调用mutations
            //this.$store.commit('changeCity',city)
            this.changeCity(city)
            this.$router.push('/')
        },
        //将mutations中的changeCity映射到methods中的changeCity方法中
        ...mapMutations(['changeCity'])
    },
    props:{
        cities:Object,
        hot:Array,
        letter:String
    },
    mounted(){
        let wrapper = this.$refs.wrapper
        this.scroll = new BScroll(wrapper)
    },
    //监听器监听letter的变化
    watch:{
        letter(){
            if(this.letter){
                const element = this.$refs[this.letter][0]
                console.log(element)
                this.scroll.scrollToElement(element)
            }
        }
    }
}
</script>

<style lang="stylus" scoped>
    .border-topbottom
        &:before
            border-color: #ccc
        &:after
            border-color: #ccc
    .border-bottom
        &:before
            border-color: #ccc
    .list
        overflow: hidden
        position absolute
        top: 1.58rem
        left: 0
        right: 0
        bottom: 0
        .title
            line-height: .6rem            
            background: #eee
            padding-left: .2rem
            color: #666
            font-size: .26rem
        .button-list
            overflow: hidden
            padding: .1rem .6rem .1rem .1rem
            .button-wrapper
                width 33.33%
                float left
                .button
                    margin: .1rem
                    padding: .1rem 0
                    text-align: center
                    border: .02rem solid #ccc
                    border-radius: .06rem
        .item-list
            .item
                line-height: .76rem
                padding-left: .2rem
</style>


