<template>
    <ul class="list">
        <li class="item" 
        v-for="(item,key) of cities" 
        :key="key"
        :ref="key"
        @click="handleLetter"
        @touchstart="handleTouchStart"
        @touchmove="handleTouchMove"
        @touchend="handleTouchEnd"
        >
            {{key}}
        </li>
    </ul>
</template>

<script>
export default {
    name:'CityAlphabet',
    props:{
        cities:Object
    },
    data(){
        return {
            touchStatus:false,
            startY:0,
            timer:null
        }
    },
    updated(){
        this.startY = this.$refs['A'][0].offsetTop
    },
    computed:{
        //声明一个城市列表的数组
        letters(){
            const letters = []
            for(let i in this.cities){
                letters.push(i)
            }
            return letters
        }
    },
    methods:{
        //字母点击事件
        handleLetter(e){
            console.log(e)
            console.log(e.target.innerText)
            //向外触发事件,并传递数据，让City.vue监听该事件
            this.$emit('change',e.target.innerText)
        },
        handleTouchStart(){
            this.touchStatus = true
        },
        handleTouchMove(e){
            if(this.touchStatus){
                
                if(this.timer){
                    clearTimeout(this.timer)
                }
                this.timer = setTimeout(() => {
                    //计算当前位置距离首字母A的距离
                    console.log(this.$refs['A'][0])
                    const startY = this.$refs['A'][0].offsetTop
                    console.log(startY)

                    //计算手指位置距离顶部的距离
                    const touchY = e.touches[0].clientY - 74;
                    console.log(touchY)

                    //计算当前手指滑动的位置对应字母的下标
                    const index = Math.floor((touchY - startY) / 20)
                    if(index >= 0 && index < this.letters.length){
                        this.$emit('change',this.letters[index])
                    }
                },16)
                                
            }
        },
        handleTouchEnd(){
            this.touchStatus = false
        }
    }
}
</script>

<style lang="stylus" scoped>
    .list
        display flex
        flex-direction column
        justify-content center
        align-items center
        position absolute        
        right 0
        top 1.58rem
        bottom 0
        width .4rem
        .item
            line-height .4rem
            text-align center
            color #00bcd4


</style>


