<template>
    <div class="header">
        <div class="header-left">
            <span class="iconfont">&#xe606;</span>
        </div> 
        <div class="header-input">
            <span class="iconfont">&#xe632;</span>
            请输入城市/景点/游玩主题
        </div>
        
        <router-link to="/city">
        <div class="header-right">
            {{this.city}}
            <span class="iconfont icon-arrow">&#xe64a;</span>
        </div>
        </router-link>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    //组件名称
    name:'HomeHeader',
    computed:{
        //展开运算符，将city映射到计算属性中
        ...mapState(['city'])
    }
}
</script>

<style lang="stylus" scoped>
    .header
        display: flex
        line-height:.86rem
        background-color: #00bcd4
        color:#fff
        .header-left
            widows 0.64rem
            float: left
        .header-input
            flex:1
            height: 0.64rem
            line-height: 0.64rem
            margin-top:.12rem
            margin-left:.2rem
            padding-left: .2rem
            background:#fff
            border-radius 0.1rem
            color:#ccc
        .header-right
            min-width: 1.6rem
            float:right 
            text-align:center
            color: #fff
            .icon-arrow
                font-size:.24rem
                
</style>


